<template>
  <div v-cloak>
    <h1 v-if="route.params.id == 'none'">新規学生</h1>
    <h1 v-else>編集学生</h1>
    <table align="center">
      <tr>
        <td style="width: 50px">
          <label>姓&emsp;</label>
        </td>
        <td style="width: 150px">
          <input type="text" name="fname" v-model="student.name" />
        </td>
        <td style="width: 50px">
          <label>名&emsp;</label>
        </td>
        <td style="width: 150px">
          <input type="text" name="lname2" v-model="student.name2" />
        </td>
      </tr>
      <tr>
        <td>
          <label>住所</label>
        </td>
        <td>
          <input type="text" name="address" v-model="student.address" />
        </td>
        <td>
          <label>年級</label>
        </td>
        <td>
          <input type="text" name="grade" v-model="student.education" />
        </td>
        <br />
      </tr>
      <tr>
        <td>
          <label>班級</label>
        </td>
        <td>
          <input type="text" name="class" v-model="student.cls" />
        </td>
        <td></td>
        <td></td>
      </tr>
    </table>
  </div>
  <div style="margin: 0 150px">
    <button type="button" @click="saveStudent">保存</button>
    <button type="button" @click="cancel">キャンセル</button>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
//import {defineComponent, reactive, toRefs, onBeforeMount, onMounted } from 'vue'
import { inject, reactive, getCurrentInstance } from "vue";
import { useRoute, useRouter } from "vue-router";

const axios = inject("axios"); //注入一下不然不能用

export default {
  name: "EditStudent",
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    const student = reactive({
      name: "",
      name2: "",
      cls: "",
      education: "",
      id: "",
      sfid: "",
      address: "",
    });
    //这里存放数据
    return {
      student,
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    saveStudent() {
      if (this.student.id) {
        this.axios.put("http://localhost:8888/student", this.student).then((response) => {
          this.cancel();
        });
      } else {
        this.axios.post("http://localhost:8888/student", this.student).then((response) => {
          this.cancel();
        });
      }
    },
    cancel() {
      this.router.push("/");
    },
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const route = useRoute();
    const router = useRouter();
    if (route.params.id != "none") {
      proxy.axios.get("http://localhost:8888/student/" + route.params.id).then((response) => {
        proxy.$data.student = response.data[0];
      });
    }
    return { route, router, proxy };
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  unmounted() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped>
table {
  border-spacing: 0;
  border-collapse: collapse;
}
td,
th {
  border: 1px solid #acbed1;
}
button {
  margin: 10px;
  width: 100px;
}
input {
  margin: 5px;
}
[v-cloak] {
  display: none !important;
}
</style>
