<template>
  <div><button @click="insertStudent()">新規</button></div>
  <table align="center">
    <tr>
      <th style="width: 120px">行数</th>
      <th style="width: 120px">Id</th>
      <th style="width: 120px">名前</th>
      <th style="width: 150px">住所</th>
      <th style="width: 50px">年級</th>
      <th style="width: 50px">班級</th>
      <th style="width: 50px"></th>
      <th style="width: 50px"></th>
    </tr>
    <tr v-for="(item, index) in studentList" :key="index">
      <td>{{ index + 1 }}</td>
      <td>{{ item.sfid }}</td>
      <td>{{ item.name + " " + item.name2 }}</td>
      <td>{{ item.address }}</td>
      <td>{{ item.education }}</td>
      <td>{{ item.cls }}</td>
      <td><button @click="editStudent(index)">編集</button></td>
      <td><button @click="deleteStudent(index)">削除</button></td>
    </tr>
  </table>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
//import {defineComponent, reactive, toRefs, onBeforeMount, onMounted } from 'vue'
import { inject } from "vue";
import { useRouter } from "vue-router";

const axios = inject("axios"); //注入一下不然不能用

export default {
  name: "StudentList",
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      studentList: [],
    };
  },
  setup() {
    const router = useRouter();
    return { router };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    insertStudent() {
      this.router.push("/edit/none");
    },
    editStudent(index) {
      this.router.push({
        name: "edit",
        params: {
          id: this.studentList[index].id,
        },
      });
    },
    deleteStudent(index) {
      this.axios.delete("http://localhost:8888/student/" + this.studentList[index].id).then(() => {
        this.studentList.splice(index, 1);
      });
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.axios.get("http://localhost:8888/student").then((response) => {
      this.studentList = response.data;
    });
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  unmounted() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped>
table {
  border-spacing: 0;
  border-collapse: collapse;
}
td,
th {
  border: 1px solid #acbed1;
}
</style>
